

























export default {
	data() {
		return {
			title: 'saveFile',
			tempFilePath: '',
			savedFilePath: ''
		}
	},
	onLoad() {
		this.savedFilePath = uni.getStorageSync('savedFilePath');
	},
	methods: {
		chooseImage() {
			uni.chooseImage({
				count: 1,
				success: (res) => {
					this.tempFilePath = res.tempFilePaths[0];
				}
			});
		},
		saveFile() {
			if (this.tempFilePath.length > 0) {
				uni.saveFile({
					tempFilePath: this.tempFilePath,
					success: (res) => {
						this.savedFilePath = res.savedFilePath;
						uni.setStorageSync('savedFilePath', res.savedFilePath);
						uni.showModal({
							title: '保存成功',
							content: '下次进入页面时，此文件仍可用',
							showCancel: false
						});
					},
					fail: (res) => {
						uni.showModal({
							title: '保存失败',
							content: '失败原因: ' + JSON.stringify(res),
							showCancel: false
						});
					}
				})
			} else {
				uni.showModal({
					content: '请选择文件',
					showCancel: false
				});
			}
		},
		clear() {
			uni.setStorageSync('savedFilePath', '');
			this.tempFilePath = '';
			this.savedFilePath = '';
		},

		openDocument() {
			uni.downloadFile({
				url: 'https://raw.githubusercontent.com/mozilla/pdf.js/master/examples/learning/helloworld.pdf',
				success: (res) => {
					uni.openDocument({
						filePath: res.tempFilePath,
						success: () => {
							console.log('打开文档成功');
						}
					});
				}
			});
		},

	}
}
